import originAxios from 'axios';

export function request(option) {
    return new Promise((resolve, reject) => {
        // 1.axios
        const instance = originAxios.create({
            baseURL: 'http://39.96.56.146:9000/',
            //baseURL: 'http://localhost:9000/',
            timeout: 10000
        });

        // 
        instance.interceptors.request.use(config => {
            // console.log('requestsuccess');
            // 1., loading, 

            // 2., token, tokenlogin

            // 3.()
            // config.data = qs.stringify(config.data)
            // console.log(config);

            // 4.
            return config
        }, err => {
            // console.log('requestfailure');
            return err
        })

        instance.interceptors.response.use(response => {
            // console.log('responsesuccess');
            return response.data
        }, err => {
            console.log('responsefailure');
            console.log(err);
            if (err && err.response) {
                switch (err.response.status) {
                    case 400:
                        err.message = ''
                        break
                    case 401:
                        err.message = ''
                        break
                    default:
                        err.message = ""
                }
            }
            return err
        })

        // 2.
        instance(option).then(res => {
            resolve(res)
        }).catch(err => {
            reject(err)
        })
    })
}
// post
export const postRequest = (url, params) => {
    return originAxios.post(`http://39.96.56.146:9000${url}`, params)
    //return originAxios.post(`http://localhost:9000${url}`, params)
};
// delete
export const deleteRequest = (url, params) => {
    return originAxios.delete(`http://39.96.56.146:9000${url}`, params)
}